.card {
  background:#deb887;
  border-radius: 3px;
  height: 265px;
  margin: 1rem;
  position: relative;
  width: 200px;
  -webkit-box-shadow: 0 5px 6px #999, 0 3px 6px #999;
          box-shadow: 0 5px 6px #999, 0 3px 6px #999;
  cursor: pointer;
}

.card:hover { 
  -webkit-transform: scale(1.15); 
      -ms-transform: scale(1.15); 
          transform: scale(1.15);
}


